<template>
    <div>
        <div>
            <b-navbar toggleable="lg" type="dark" variant="purple" style="padding: 2rem 1rem !important;">
                <b-navbar-brand href="#" style="font-size: 1.35rem !important; color: white !important;">{{name}}</b-navbar-brand>

                <b-navbar-toggle target="nav_collapse" />

                <b-collapse is-nav id="nav_collapse">
                    <!-- Right aligned nav items -->
                    <b-navbar-nav class="ml-auto">
                        <b-nav-form>
                            <label class="sr-only" for="firstName">Name</label>
                            <b-input class="mb-2 mr-sm-2 mb-sm-0" id="firstName" placeholder="Jane" v-model="form.firstName"/>

                            <label class="sr-only" for="lastName">Name</label>
                            <b-input class="mb-2 mr-sm-2 mb-sm-0" id="lastName" placeholder="Doe" v-model="form.lastName"/>


                            <label class="sr-only" for="email">Email</label>
                            <b-input class="mb-2 mr-sm-2 mb-sm-0" id="email" placeholder="jane.doe@email.com" type="email" v-model="form.email"/>

                            <b-button variant="pink" v-on:click="setupChallenge">Create Okta Org</b-button>
                        </b-nav-form>

                    </b-navbar-nav>
                </b-collapse>
            </b-navbar>
        </div>

        <b-container>
            <b-row align-h="center" v-if="!org.domain && errors.length">
                <b-col cols="8">
                    <p/>
                    <b-card header="Please correct the following error(s):">
                        <b-card-text>
                            <ul>
                                <li v-for="error in errors">{{error}}</li>
                            </ul>
                        </b-card-text>
                    </b-card>
                </b-col>
            </b-row>
            <b-row align-h="center">
                <b-col cols="8">
                    <div v-if="busy">
                        <h4><img src="../assets/fidget.gif" width="50px">{{busyMsg}}</h4>
                        <b-progress :value="counter" :max="max" variant="purple" show-progress animated/>
                    </div>
                </b-col>
            </b-row>
            <p/>
        </b-container>
        <b-container v-if="org.domain">
            <p/>
            <b-row>
                <b-col><h2 class="text-center">Your Okta Org</h2></b-col>
            </b-row>
            <b-row align-h="center">
                <b-col cols="8">
                    <table class="table table-striped table-hover">
                        <tbody>
                        <tr>
                            <th scope="row">Domain:</th>
                            <td><nobr>{{org.domain}}</nobr></td>
                        </tr>
                        <tr>
                            <th scope="row"><nobr>Admin email:</nobr></th>
                            <td>{{org.adminEmail}}</td>
                        </tr>
                        <tr>
                            <th scope="row"><nobr>Admin password:</nobr></th>
                            <td>{{org.adminPassword}}</td>
                        </tr>
                        <tr>
                            <th scope="row"><nobr>API token:</nobr></th>
                            <td><nobr>{{org.apiToken}}</nobr></td>
                        </tr>
                        </tbody>
                    </table>
                </b-col>
            </b-row>
            <b-row>
                <b-col><h5 class="text-center">
                    (NOTE: This information will be cleared from the browser after you've downloaded the
                    Postman collection, but is included as variables in the Postman collection.)
                </h5></b-col>
            </b-row>
            <b-row>
                <b-col class="text-center">
                    <!-- Not using the modal for Oktane. Might want to use it for other confs. -->
                    <!--<b-button variant="purple" v-b-modal.postmanModal>Download Postman Collection</b-button>-->
                    <b-button variant="purple" v-on:click="getPostmanCollection">Download Postman Collection</b-button>
                </b-col>
            </b-row>
            <p/>
        </b-container>
        <div class="card-text" v-if="secretClick">
            <b-row aign-h="start">
                <b-col cols="2">
                    <b-input id="orgName" placeholder="Org Name" v-model="form.orgName"/>
                </b-col>
                <b-col cols="2">
                    <b-button variant="danger" v-on:click="setOrgName">Set Org Name</b-button>
                </b-col>
            </b-row>
            <p/>
        </div>
        <div>
            <b-card no-body>
                <b-tabs pills card v-model="tabIndex">
                    <b-tab title="Tier 1: Hacker" active :title-link-class="linkClass(0)">
                        <Tier1/>
                    </b-tab>
                    <b-tab title="Tier 2: Advanced" :title-link-class="linkClass(1)">
                        <Tier2/>
                    </b-tab>
                </b-tabs>
            </b-card>
        </div>
        <div>
            <b-modal no-close-on-esc no-close-on-backdrop hide-header-close ok-variant="purple"
                 size="huge" id="postmanModal" title="Caution!" @ok="getPostmanCollection"
            >
                <p>
                    This information will be cleared from the browser after you've downloaded the
                    Postman collection. But, it is included in the Postman collection.
                </p>
                <table class="table table-striped">
                    <tbody>
                        <tr>
                            <th scope="row"><nobr>domain:</nobr></th>
                            <td><nobr>{{org.domain}}</nobr></td>
                        </tr>
                        <tr>
                            <th scope="row"><nobr>admin email:</nobr></th>
                            <td><nobr>{{org.adminEmail}}</nobr></td>
                        </tr>
                        <tr>
                            <th scope="row"><nobr>admin password:</nobr></th>
                            <td><nobr>{{org.adminPassword}}</nobr></td>
                        </tr>
                        <tr>
                            <th scope="row"><nobr>api token:</nobr></th>
                            <td><nobr>{{org.apiToken}}</nobr></td>
                        </tr>
                    </tbody>
                </table>
            </b-modal>
        </div>
        <div>
            <b-modal size="huge" ref="errorModal" title="Oops!">
                <p>
                    Something's gone horribly wrong!<br/>
                    Try again...
                </p>
                <p>
                    If this happens again, let our staff know.
                </p>
            </b-modal>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'

    import appConfig from '../.app.config'
    import orgScoreStore from '../model/orgScoreStore'

    import Tier1 from '@/components/Tier1'
    import Tier2 from '@/components/Tier2'

    export default {
        name: 'hello',
        components: {
          Tier1, Tier2
        },
        data() {
            return {
                name: appConfig.event.name,
                errors: [],
                form: {
                    firstName: '',
                    lastName: '',
                    email: '',
                    orgName: ''
                },
                busy: false,
                busyMsg: '',
                org: {},
                counter: 0,
                max: 100,
                secretClick: false,
                scores: orgScoreStore.state.scores,
                orgName: orgScoreStore.state.org,
                tabIndex: 0
            }
        },
        computed: {
          orgScore: function () {
              if (!this.scores.READ_ONLY_API_TOKEN) { return undefined }
              var score = 0
              Object.values(this.scores).forEach(value => score += value)
              return score
          }
        },
        mounted: function () {
            orgScoreStore.init()
            var that = this
            window.addEventListener('click', function (evt) {
                if (evt.detail === 3) {
                    that.secretClick = !that.secretClick
                }
            });
        },
        methods: {
            linkClass: function (idx) {
                if (this.tabIndex === idx) {
                    return ['bg-purple', 'text-light']
                } else {
                    return ['bg-light', 'text-purple']
                }
            },
            clearData: function () {
              this.errors = []
              this.form.firstName = ''
              this.form.lastName = ''
              this.form.email = ''
              this.org = {}
            },
            checkForm: function () {
                this.errors = []
                if (!this.form.firstName) {
                    this.errors.push("First name is required.")
                }
                if (!this.form.lastName) {
                    this.errors.push("Last name is required.")
                }
                if (!this.form.email) {
                    this.errors.push("Email is required.")
                } else if (!this.validEmail(this.form.email)) {
                    this.errors.push(this.form.email + ' is not a valid email address')
                }
                return !this.errors.length
            },
            validEmail: function (email) {
                var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                return re.test(email);
            },
            setupChallenge: async function () {
                if (!this.checkForm()) {
                    return;
                }
                this.busy = true
                orgScoreStore.reset()
                this.createOrg()
                    .then(this.setAdminPassword)
                    .then(this.createUser)
                    .then((response) => {
                        orgScoreStore.setOrgEmail(response.data.email)
                    })
                    .then(this.registerForChallenge)
                    .then(this.createApiToken)
                    .then((response) => {
                        this.org.domain = response.data.subdomain + '.' + response.data.domain
                        //this.org.adminEmail = response.data.adminEmail
                        this.org.adminEmail = orgScoreStore.state.org.email
                        this.org.adminPassword = response.data.adminPassword
                        this.org.apiToken = response.data.apiToken
                        this.busy = false
                        this.counter = 0
                        orgScoreStore.setOrgName(response.data.subdomain)
                    })
                    .catch((err) => {
                        console.error(err.response)
                        this.counter = 0
                        this.busy = false
                        this.$refs.errorModal.show()
                    })
            },
            createOrg: async function () {
                this.counter += 20
                this.busyMsg = 'Creating Okta Org...'
                return axios.post(
                    appConfig.resourceServer.orgUrl,
                    {
                        firstName: this.form.firstName,
                        lastName: this.form.lastName,
                        email: this.form.email
                    },
                    { withCredentials: true }
                )
            },
            setAdminPassword: async function () {
                this.counter += 20
                this.busyMsg = 'Setting Admin Password...'
                return axios.post(
                    appConfig.resourceServer.adminPasswordUrl,
                    {},
                    { withCredentials: true }
                )
            },
            createUser: async function () {
                this.counter += 20
                this.busyMsg = 'Creating User: ' +
                    this.form.firstName + ' ' + this.form.lastName + ' with Super Admin role...'
                return axios.post(
                    appConfig.resourceServer.userUrl,
                    {},
                    { withCredentials: true }
                )
            },
            registerForChallenge: async function () {
                this.counter += 20
                this.busyMsg = 'Registering for the Challenge...'
                return axios.post(
                    appConfig.resourceServer.challengeUrl,
                    {},
                    { withCredentials: true }
                )
            },
            createApiToken: async function () {
                this.counter += 20
                this.busyMsg = 'Creating API Token...'
                return axios.post(
                    appConfig.resourceServer.apiTokenUrl,
                    {},
                    { withCredentials: true }
                )
            },
            getPostmanCollection: async function () {
                return axios({
                    url: appConfig.resourceServer.postmanUrl,
                    method: 'GET',
                    responseType: 'blob',
                    withCredentials: true
                }).then((response) => {
                    const url = window.URL.createObjectURL(new Blob([response.data]));
                    const link = document.createElement('a');
                    link.href = url;
                    link.setAttribute('download', 'postman.txt');
                    document.body.appendChild(link);
                    link.click();
                    this.busy = false
                }).then(() => {
                    this.clearData()
                }).catch((response) => {
                    console.log(response)
                    this.busy = false
                })
            },
            setOrgName: function () {
                orgScoreStore.setOrgName(this.form.orgName)
                this.secretClick = false
            }
        }
    }

</script>

