import Vue from 'vue'
import axios from 'axios'

import appConfig from '../.app.config'

const orgNameSessionKey = 'orgName'
const orgEmailSessionKey = 'email'

export default {
    orgNameSessionKey: orgNameSessionKey,
    state: {
        org: {},
        scores: {}
    },
    reset: function () {
        delete this.state.org.name
        delete this.state.org.email
        localStorage.removeItem(orgNameSessionKey)
        localStorage.removeItem(orgEmailSessionKey)
        this.resetScores()
    },
    resetScores: function () {
        for (var member in this.state.scores) {
            delete this.state.scores[member]
        }
    },
    setScores: function (scores) {
        this.resetScores()
        Object.keys(scores).forEach((key) => {
            Vue.set(this.state.scores, key, scores[key])
        })
    },
    setOrgName: function (orgName) {
        Vue.set(this.state.org, 'name', orgName)
        localStorage.setItem(orgNameSessionKey, orgName)
    },
    setOrgEmail: function (email) {
        Vue.set(this.state.org, 'email', email)
        localStorage.setItem(orgEmailSessionKey, email)
    },
    init: function () {
        if (localStorage.getItem(orgNameSessionKey)) {
            this.setOrgName(localStorage.getItem(orgNameSessionKey))
        }
        if (localStorage.getItem(orgEmailSessionKey)) {
            this.setOrgEmail(localStorage.getItem(orgEmailSessionKey))
        }
        this.loadScores()
        setInterval(function () {
            this.loadScores()
        }.bind(this), 10000)
    },
    loadScores: async function () {
        if (!this.state.org.name) return
        var response = await axios.get(appConfig.resourceServer.scoreUri + '/' + this.state.org.name)
        if (response.data && response.data.challengeStats && response.data.challengeStats.scores) {
            this.setScores(response.data.challengeStats.scores)
        }
    }
}