<template>
    <div>
        <b-card no-body>
            <b-tabs card v-model="tabIndex">
                <b-tab title="Overview" active :title-link-class="linkClass(0)">
                    <p class="card-text">
                        The advanced tier is all about Okta's
                        <a href="https://developer.okta.com/use_cases/inline_hooks" target="_blank">Hooks</a>
                        feature. Hooks are webhooks which allow you to programatically hook into the middle of a
                        workflow with Okta to alter behavior and responses from API calls and events.
                    </p>
                    <p class="card-text">
                        You'll be using your Okta Admin Console,
                        <a href="https://www.getpostman.com" target="_blank">Postman</a>, and the
                        <a href="https://glitch.com/" target="_blank">Glitch</a> hosted app service to complete the
                        advanced tier.
                    </p>
                    <p class="card-text">
                        In our advanced tier there's a little less automated hand-holding; if you
                        get jammed up with anything, head over the Developer Hub and our team will happy to help.
                    </p>
                    <p class="card-text">
                        <b>NOTE:</b> It is not a requirement that you complete Tier 1 in order to complete Tier 2
                    </p>
                    <p class="card-text">
                        When you get to the last tab, you'll show off your work to one of our team members at the
                        Developer Hub and collect your {{tier2Prize}}!
                    </p>
                    <p class="card-text">
                        If you've already completed Tier 1, you can move on to the <a href="#" @click="tabIndex++">
                        next tab</a>.
                    </p>
                    <PostmanImport/>
                    <p class="card-text">
                        You can now move on to the <a href="#" @click="tabIndex++">next tab</a>.
                    </p>
                </b-tab>
                <b-tab title="Step 1: Demo App" :title-link-class="linkClass(1)">
                    <Tier2ProgressBar/>
                    <p class="card-text">
                        We've created a web app called <code>AVBank</code>. We'll be using it throughout the advanced
                        tier. The good news is that you can get this app working for yourself in about 30 seconds,
                        thanks to Glitch.
                    </p>
                    <p class="card-text">
                        Navigate to the base app: <a href="https://glitch.me/edit/#!/okta-conf-avbank" target="_blank">
                        okta-conf-avbank</a>.
                    </p>
                    <p class="card-text">
                        From there, you'll create your own copy of the app. In Glitch lingo, this is called
                        <code>Remixing</code>. Click the <code>okta-conf-avbank</code> dropdown and select
                        <code>Remix Project</code>.
                    </p>
                    <p class="card-text">
                        You'll be assigned a Glitch subdomain which replaces <code>okta-conf-avbank</code>.
                        Make note of this subdomain as you'll need it later.
                    </p>
                    <p class="card-text">
                        <b>NOTE:</b> This is a good time to register for a Glitch account if you don't already have one.
                        It's not a requirement, but if you want to save your work and return to it later, you'll need a
                        Glitch account.
                    </p>
                    <p class="card-text">
                        Before you can see the app in action, you need to do some setup in your Okta org. You'll do
                        this in Postman:
                    </p>
                    <ol>
                        <li>
                            Open up the settings for the <code>{{eventName}}</code> collection<br/>
                            <img src="../assets/postman_settings.png"/>
                        </li>
                        <li>Click <code>Variables</code></li>
                        <li>Fill in the value for <code>my-glitch-avbank</code> with your Glitch subdomain</li>
                        <li>Click <code>Update</code></li>
                        <li>Expand the <code>{{eventName}}</code> collection</li>
                        <li>Expand the <code>Applications</code> folder</li>
                        <li>Select the <code>Create OIDC Application</code> request</li>
                        <li>Click <code>Send</code></li>
                    </ol>
                    <p class="card-text">
                        This creates an OpenID Connect application in your Okta Org. You'll configure the AVBank App
                        below to use this application to enable authentication.
                    </p>
                    <p class="card-text">
                        Scroll down and you'll see values for <code>client_id</code> and <code>client_secret</code>.
                        Copy those values to an editor of your choice as you'll need them shortly.
                    </p>
                    <AssignGroupToApp appName="OIDC for AVBank"/>
                    <p class="card-text">
                        <i>You can find more information in the
                        <a href="https://developer.okta.com/docs/api/resources/oidc/" target="_blank">
                            OpenID Connect & OAuth 2.0 API</a> section of the documentation.</i>
                    </p>
                    <p class="card-text">
                        Back in your Glitch app, click on the <code>.env</code> file on the left.
                    </p>
                    <ol>
                        <li>Set <code>OKTA_URL</code> to the <code>url</code> value from your Postman collection
                            variables</li>
                        <li>Set the <code>ISSUER</code> to the same value, but with <code>/oauth2/default</code> tacked
                            on to the end</li>
                        <li>Set the <code>CLIENT_ID</code> and <code>CLIENT_SECRET</code> values to the values you
                            collected from Postman earlier when you created the OIDC application</li>
                        <li>Set the <code>OKTA_API_TOKEN</code> value to the <code>apikey</code> value from
                            your Postman collection variables</li>
                    </ol>
                    <p class="card-text">
                        <img src="../assets/avbank_settings.png"/>
                    </p>
                    <p class="card-text">
                        Click <code>Show > In a New Window</code> to see the real-time instance of your version of the app in action.
                        You should be able to log in as the user you created your Okta Org with. Notice that you're
                        redirected to Okta to authenticate and then sent back to your Glitch app. This is part of
                        the configuration of the application in Okta from the Postman request earlier.
                    </p>
                    <p class="card-text">
                        If you want a sneak peak of what you'll be working on later, click
                        <code>View Tokens</code> after you've authenticated. You'll see the values of the security
                        tokens used within the application. Later, we'll configure a webhook that will alter the contents
                        of these tokens on the fly!
                    </p>
                    <p class="card-text">
                        <b>NOTE:</b> You can always get to the live version of a Glitch app by browsing to:
                        <code>https://&lt;my-glitch-subdomain&gt;.glitch.me</code>.
                    </p>
                    <p class="card-text">
                        With your AVBank App set up, let's continue on to the <a href="#" @click="tabIndex++"
                        >next step</a>.
                    </p>
                </b-tab>
                <b-tab title="Step 2: Events" :title-link-class="linkClass(2)">
                    <Tier2ProgressBar/>
                    <p class="card-text">
                        Our first look at the world of webhooks will be <code>Event Hooks</code>. In this step, you'll
                        configure Okta Hooks along with another Glitch app to capture and output all supported
                        events (for instance, logins).
                    </p>
                    <p class="card-text">
                        To start, navigate to the base Glitch app for webhooks:
                        <a href="https://glitch.me/edit/#!/okta-conf-hooks" target="_blank">okta-conf-hooks</a>
                    </p>
                    <p class="card-text">
                        <code>Remix</code> as you did in Step 1. Capture the Glitch subdomain you're assigned, then
                        update the Postman collection variable:
                    </p>
                    <ol>
                        <li>
                            Open the settings for the <code>{{eventName}}</code> collection<br/>
                            <img src="../assets/postman_settings.png"/>
                        </li>
                        <li>Click <code>Variables</code></li>
                        <li>Fill in the value for <code>my-glitch-hooks</code> with your Glitch subdomain</li>
                        <li>Click <code>Update</code></li>
                        <li>Expand the <code>{{eventName}}</code> collection</li>
                        <li>Expand the <code>Hooks</code> folder</li>
                        <li>Select the <code>Create Event Hook for all events</code> request</li>
                        <li>Click <code>Send</code></li>
                    </ol>
                    <p class="card-text">
                        Capture the webhook <code>id</code> from the response and set the <code>hook-id</code> value in
                        the Postman collection variables with that value.
                    </p>
                    <ol>
                        <li>Select the <code>Verify Event Hook by ID</code> request</li>
                        <li>Click <code>Send</code></li>

                    </ol>
                    <p class="card-text">
                        Notice that the API request has the <code>hook-id</code> value as part of the path. This is
                        filled in by Postman from the collection variables.
                    </p>
                    <p class="card-text">
                        Click <code>Show > In a New Window</code> in your Glitch app and you'll see the
                        <code>Real-time Hooks Viewer</code> app. There's not much to see just yet, but there will be in
                        just a moment.
                    </p>
                    <p class="card-text">
                        In an incognito window, log in to your Okta Org. Return to the <code>Hooks Viewer</code> app and you should
                        see the <code>user.session.start</code> event output there.
                    </p>
                    <p class="card-text">
                        <b>Note:</b> The output from the <code>Hooks Viewer</code> app can get cluttered pretty quickly.
                        You can always click <code>clear</code> in the upper right to clear the screen.
                    </p>
                    <p class="card-text">
                        Event Hooks are useful, but passive - they're just informational. In the
                        <a href="#" @click="tabIndex++">next step</a>, you'll create a Registration Hook which will
                        impact registration validation in real-time.
                    </p>
                </b-tab>
                <b-tab title="Step 3: Registration" :title-link-class="linkClass(3)">
                    <Tier2ProgressBar/>
                    <p class="card-text">
                        In this step, you'll create an <code>Inline Registration Hook</code> that will actively impact
                        the validation for a user's registration.
                    </p>
                    <p class="card-text">
                        Imagine that you collect a user's social security number as part of your registration flow.
                        And, you want to verify that social security number using an external service. The response from
                        the external service will determine if the registration is allowed to complete and what (if any)
                        error is shown on the registration form.
                    </p>
                    <p class="card-text">
                        The first thing we'll tackle in this step is to add some profile properties to the default
                        profile schema for your Okta Org. This is all set up for you in Postman.
                    </p>
                    <ol>
                        <li>Expand the <code>{{eventName}}</code> collection</li>
                        <li>Expand the <code>Schemas</code> folder</li>
                        <li>Select the <code>Add User Profile Properties</code> request</li>
                        <li>Click <code>Send</code></li>
                    </ol>
                    <p class="card-text">
                        If you look at the body of this API call, it is adding three attributes to the default profile
                        schema: <code>memberNumber</code>, <code>memberLevel</code>, and <code>ssn</code>. You'll be
                        making use of these attributes in the following instructions.
                    </p>
                    <p class="card-text">
                        Next up, we'll set up the Registration Hook.
                    </p>
                    <ol>
                        <li>Expand the <code>{{eventName}}</code> collection</li>
                        <li>Expand the <code>Hooks</code> folder</li>
                        <li>Select the <code>Create Inline Registration Hook</code> request</li>
                        <li>Click <code>Send</code></li>
                    </ol>
                    <p class="card-text">
                        Notice the <code>type</code> of the hook in the request is
                        <code>com.okta.user.pre-registration</code>. And, the <code>uri</code> points to the Glitch app
                        you Remixed. Now, when you attempt to register as a new user, this webhook will cause an
                        outbound request to the specified <code>uri</code>. This happens before the registration is
                        completed.
                    </p>
                    <p class="card-text">
                        You need to enable self service registration and add the new attributes to the
                        registration form. This is done in your Okta admin console. Log in to your Okta Org with your
                        admin user.
                    </p>
                    <ol>
                        <li>Navigate to <code>Users</code> > <code>Registration</code></li>
                        <li>Click <code>Enable Registration</code></li>
                        <li>Select <code>Registration inline hook with account verification</code> for the
                        <code>Extension</code> field</li>
                        <li>In the <code>Registration Form</code> section, use the <code>Add Field</code> button to
                        add in the <code>memberNumber</code> and <code>ssn</code> fields</li>
                        <li>Uncheck <code>Activation requirements</code></li>
                        <li>Click <code>Save</code></li>
                    </ol>
                    <p class="card-text">
                        Open an incognito window and navigate to your Okta Org.
                    </p>
                    <ol>
                        <li>Click the <code>Sign up</code> link at the bottom of the sign in form</li>
                        <li>For the <code>Email</code> field, enter <code>bob@platinum.example.com</code></li>
                        <li>Enter <code>123456aA$</code> in the <code>Password</code> field</li>
                        <li>Enter <code>Bob</code> and <code>Belcher</code> for the <code>First</code> and
                        <code>Last</code> name fields, respectively</li>
                        <li>Enter <code>12345678</code> for the <code>Member Number</code> field</li>
                        <li>Enter <code>111223334</code> for the <code>SSN</code> field. <b>NOTE:</b> This value will
                        NOT be found when your webhook code attempts to look up the SSN</li>
                        <li>Click <code>Register</code></li>
                    </ol>
                    <p class="card-text">
                        You should see the error message <code>Unable to verify user by SSN and MemberId</code>. If
                        you look back at the <code>Real-time Hooks View</code> again, you should see a number of
                        requests, including the last one which shows <code>registration: DENY</code>.
                    </p>
                    <p class="card-text">
                        Now, replace the <code>1112223334</code> with <code>111223333</code>. This is the correct SSN.
                        Click <code>Register</code> once again. This time, the registration completes. Set a security
                        question and answer and choose an image. Click <code>Create My account</code>.
                    </p>
                    <p class="card-text">
                        Look back at the <code>Real-time Hooks View</code> again. You'll see a number of events related
                        to the user registration. Scroll down until you find the log entry for the Registration Hook.
                        Notice that the <code>contextMessage</code> shows that the registration succeeded. Also, the
                        <code>memberLevel</code> attribute on the user profile was automatically set to
                        <code>Platinum</code>.
                    </p>
                    <p class="card-text">
                        You can confirm this by clicking <code>Bob</code> and <code>Settings</code> from the Okta
                        dashboard. You'll see that the <code>Member Level</code> is set to <code>Platinum</code>.
                    </p>
                    <p class="card-text">
                        In the <a href="#" @click="tabIndex++">next step</a>, you'll create an authorization webhook
                        (using Okta's API Access Management feature) to alter the contents of tokens in real-time.
                    </p>
                </b-tab>
                <b-tab title="Step 4: Authorization" :title-link-class="linkClass(4)">
                    <Tier2ProgressBar/>
                    <p class="card-text">
                        In this step, you'll create a <code>token transform</code> webhook to alter the contents of the
                        <code>access_token</code> and the <code>id_token</code> used with OpenID Connect and OAuth 2.0.
                    </p>
                    <p class="card-text">
                        <i>You can find more information in the
                        <a href="https://developer.okta.com/docs/api/resources/oidc/" target="_blank">
                        OpenID Connect & OAuth 2.0 API</a> section of the documentation.</i>
                    </p>
                    <p class="card-text">
                        First, we'll set up the webhook in Postman.
                    </p>
                    <ol>
                        <li>Expand the <code>{{eventName}}</code> collection</li>
                        <li>Expand the <code>Hooks</code> folder</li>
                        <li>Select the <code>Register APIAM Token Hook</code> request</li>
                        <li>Click <code>Send</code></li>
                    </ol>
                    <p class="card-text">
                        Notice, this time the <code>type</code> of the webhook is
                        <code>com.okta.oauth2.tokens.transform</code> and the <code>uri</code> refers back to your
                        Glitch app. This webhook can dynamically update the contents of the tokens in flight, on their
                        way back to the <code>AVBank</code> app after you've authenticated.
                    </p>
                    <p class="card-text">
                        Next, you'll update your Okta Org to use the webhook. Switch over to Okta Org admin console.
                    </p>
                    <ol>
                        <li>Navigate to <code>API</code> > <code>Authorization Servers</code></li>
                        <li>Click <code>default</code></li>
                        <li>Click <code>Access Policies</code></li>
                        <li>Click the pencil icon to the right of the <code>Default Policy Rule</code></li>
                        <li>Update the <code>Use this inline hook</code> dropdown to <code>APIAM Token Hook with DB
                            lookup</code>
                        <li>Click <code>Update Rule</code></li>
                    </ol>
                    <p class="card-text">
                        In an incognito window, navigate to your <code>AVBank</code> app. Log in as
                        <code>bob@platinum.example.com</code> and click <code>View Tokens</code>. Notice that
                        <code>id_token</code> contains <code>SSN4</code> as well as <code>memberLevel</code>.
                        This is the work of the webhook that transforms the contents of the token on the fly.
                    </p>
                    <p class="card-text">
                        In the <a href="#" @click="tabIndex++">next step</a>, you'll review the
                        <code>Real-time Hooks View</code> once again to see the webhook action log.
                    </p>
                </b-tab>
                <b-tab title="Show Off" :title-link-class="linkClass(5)">
                    <Tier2ProgressBar/>
                    <p class="card-text">
                        Review the <code>Real-time Hooks View</code>. Scroll down until you see
                        <code>/okta/hooks/oidc-token/dblookup</code>. Below that, you'll see the details of the patch
                        operation including the <code>SSN4</code> and <code>memberLevel</code> values. An example
                        of this shown in the image below.
                    </p>
                    <p class="card-text">
                        <img src="../assets/token_example.png"/>
                    </p>
                    <p class="card-text">
                        <b>Show your screen to any of the staff at the Developer Hub and collect your {{tier2Prize}}!</b>
                    </p>
                </b-tab>
            </b-tabs>
        </b-card>
    </div>
</template>

<script>
    import Tier2ProgressBar from '@/components/Tier2ProgressBar'
    import PostmanImport from '@/components/PostmanImport'
    import AssignGroupToApp from '@/components/AssignGroupToApp'

    import appConfig from '../.app.config'

    export default {
        components: {
          Tier2ProgressBar, PostmanImport, AssignGroupToApp
        },
        data() {
            return {
                tabIndex: 0,
                eventName: appConfig.event.name,
                tier2Prize: appConfig.tier2Prize
            }
        },
        methods: {
            linkClass: function (idx) {
                if (this.tabIndex === idx) {
                    return ['bg-purple', 'text-light']
                } else {
                    return ['bg-light', 'text-purple']
                }
            }
        }
    }
</script>