<template>
    <div>
        <p class="card-text" v-if="scores.MANAGE_ADDITIONAL_APP">
            <b-progress :max="max" height="1.75rem" style="font-size: 1.15rem;" :variant="progressVariant" show-progress>
                <b-progress-bar :value="orgScore" :label="stepProgress"/>
            </b-progress>
            <br/>
        </p>
    </div>
</template>

<script>
    import orgScoreStore from '../model/orgScoreStore'

    export default {
        data() {
            return {
                scores: orgScoreStore.state.scores,
                max: 40
            }
        },
        methods: {
        },
        computed: {
            orgScore: function () {
                if (!this.scores.MANAGE_ADDITIONAL_APP) { return undefined }
                var score = 0
                if (this.scores.MANAGE_ADDITIONAL_APP) score += this.scores.MANAGE_ADDITIONAL_APP
                if (this.scores.HOOKS_EVENT_HOOKS) score += this.scores.HOOKS_EVENT_HOOKS
                if (this.scores.HOOKS_REGISTRATION_HOOK) score += this.scores.HOOKS_REGISTRATION_HOOK
                if (this.scores.HOOKS_APIAM_HOOKS) score += this.scores.HOOKS_APIAM_HOOKS
                return score
            },
            stepProgress: function () {
                var progress = []
                if (this.scores.MANAGE_ADDITIONAL_APP) progress.push(1)
                if (this.scores.HOOKS_EVENT_HOOKS) progress.push(2)
                if (this.scores.HOOKS_REGISTRATION_HOOK) progress.push(3)
                if (this.scores.HOOKS_APIAM_HOOKS) progress.push(4)

                return 'Steps Completed: ' + (progress.join(', ')) + ' of 4'
            },
            progressVariant: function () {
                if (this.orgScore < 40) return 'pink'
                return 'success'
            }
        }
    }
</script>