                        Postman API request tool</a> in order to proceed.
                    </p>
                    <p  class="card-text">
                        The first step is completed just by creating an Okta Org! You'll see a progress bar appear on
                        each tab as you move through the challenge. Next, you'll download a custom Postman collection of
                        API requests to execute each of the subsequent steps. The Postman collection has all of the
                        necessary variables pre-configured.
                    </p>
                    <p class="card-text">
                        <b>NOTE:</b> It can take as long as 30 seconds to update the progress bar after you've completed
                        a step.
