                    <p>
                        <i>You can find more information on any of the Okta APIs in the
                        <a href="https://developer.okta.com/reference/" target="_blank">
                            API Reference</a> section of the documentation.</i>

                    </p>
                </b-tab>
                <b-tab title="Step 2: Create Users" :title-link-class="linkClass(1)">
                    <Tier1ProgressBar/>
                    <PostmanImport/>
                    <p class="card-text">
                        This first API call is all set up for you:
                    </p>
                    <ol>
                        <li>Expand the <code>{{eventName}}</code> collection</li>
                        <li>Expand the <code>Users</code> folder</li>
                        <li>Select the <code>Create Activated User with Credentials</code> request</li>
                        <li>Click <code>Send</code></li>
                    </ol>
                    <p class="card-text">
                        This will create a new User in your Okta org using the Okta API. Take a look at the
                        <code>Headers</code> and <code>Body</code> tabs in Postman to see what's going on.
                    </p>
                    <p class="card-text">
                        Move on to the <a href="#" @click="tabIndex++">next step</a> to create groups.
                    </p>
                    <p class="card-text">
                        <i>You can find more information in the
                        <a href="https://developer.okta.com/docs/api/resources/users/"
                           variant="purple" target="_blank">Users API</a> section of the documentation.</i>
                    </p>
                </b-tab>
                <b-tab title="Step 3: Create Groups" :title-link-class="linkClass(2)">
                    <Tier1ProgressBar/>
                    <p class="card-text">
                        Let's create a Group!
                    </p>
                    <p class="card-text">
                        For this step you'll need to fill in some details for the body of the API call.
                    </p>
                    <ol>
                        <li>Expand the <code>{{eventName}}</code> collection</li>
                        <li>Expand the <code>Groups</code> folder</li>
                        <li>Select the <code>Add Group</code> request</li>
                        <li>Click the <code>Body</code> tab</li>
                    </ol>
                    <p class="card-text">
