                           variant="purple" target="_blank">Groups API</a> section of the documentation.</i>
                    </p>
                </b-tab>
                <b-tab title="Step 4: Create Apps" :title-link-class="linkClass(3)">
                    <Tier1ProgressBar/>
                    <p class="card-text">
                        Let's create an App!
                    </p>
                    <p class="card-text">
                        In this step, you'll create a simple Bookmark application using an API call.
                    </p>
                    <ol>
                        <li>Expand the <code>{{eventName}}</code> collection</li>
                        <li>Expand the <code>Applications</code> folder</li>
                        <li>Select the <code>Create Bookmark Application</code> request</li>
                        <li>Click the <code>Body</code> tab</li>
