                    </p>
                </b-tab>
                <b-tab title="Step 5: Assign Groups" :title-link-class="linkClass(4)">
                    <Tier1ProgressBar/>
                    <AssignGroupToApp appName="Conf Score Checker"/>
                    <p class="card-text">
                        Move on to the <a href="#" @click="tabIndex++">next tab</a> to finish off tier 1.
                    </p>
                    <p class="card-text">
                        <i>You can find more information in the
                        <a href="https://developer.okta.com/docs/api/resources/apps/#assign-group-to-application"
