                            section of the documentation.</i>
                    </p>
                </b-tab>
                <b-tab title="GOTO Live Login" :title-link-class="linkClass(5)">
                    <Tier1ProgressBar/>
                    <p class="card-text" v-if="orgScore >= 50">
                        You did it! You completed all the steps to get a {{tier1Prize}}!
                    </p>
                    <p class="card-text" v-if="orgScore < 50">
                        Almost there! You've got {{orgScore/10}} of 5 steps done. Return to the prior tabs to see which ones you
                        haven't yet scored and then come back here.
                    </p>
                    <p class="card-text">
                        Now, log in to your Okta Org as the user you created earlier and
                        launch the <code>Conf Score Checker</code> app which will confirm that you've earned your
                        {{tier1Prize}}.
                    </p>
                    <table class="table table-striped table-hover w-25" v-if="orgName.name">
                        <tbody>
                        <tr>
                            <th scope="row">Username:</th>
                            <td>{{orgName.email}}</td>
                        </tr>
                        <tr>
                            <th scope="row"><nobr>Password:</nobr></th>
                            <td>123456aA$</td>
                        </tr>
                        <tr>
                            <th scope="row">Org:</th>
                            <td>
                                <nobr><a v-bind:href="orgUrlWithFullPath" target="_blank">{{orgUrl}}</a></nobr>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                </b-tab>
            </b-tabs>
        </b-card>
    </div>
</template>

<script>
    import orgScoreStore from '../model/orgScoreStore'

    import Tier1ProgressBar from '@/components/Tier1ProgressBar'
    import PostmanImport from '@/components/PostmanImport'
    import AssignGroupToApp from '@/components/AssignGroupToApp'

    export default {
        components: {
            Tier1ProgressBar, PostmanImport, AssignGroupToApp
        },
        data() {
            return {
                orgName: orgScoreStore.state.org,
                scores: orgScoreStore.state.scores,
                tabIndex: 0,
                eventName: appConfig.event.name,
                tier1Prize: appConfig.tier1Prize
            }
        },
        methods: {
            linkClass: function (idx) {
                if (this.tabIndex === idx) {
                    return ['bg-purple', 'text-light']
                } else {
                    return ['bg-light', 'text-purple']
                }
            }
        },
        computed: {
            orgScore: function () {
                if (!this.scores.READ_ONLY_API_TOKEN) { return undefined }
                var score = 0
                Object.values(this.scores).forEach(value => score += value)
                return score
            },
            orgUrl: function () {
                return 'https://' + this.orgName.name + '.oktapreview.com'
            },
            orgUrlWithFullPath: function () {
                return 'https://' + this.orgName.name + '.oktapreview.com/app/UserHome?fromAdmin=true'
            }
        }
    }
</script>