<template>
    <div>
        <p class="card-text" v-if="scores.READ_ONLY_API_TOKEN">
            <b-progress :max="max" height="1.75rem" style="font-size: 1.15rem;" :variant="progressVariant" show-progress>
                <b-progress-bar :value="orgScore" :label="stepProgress"/>
            </b-progress>
            <br/>
        </p>
    </div>
</template>

<script>
    import orgScoreStore from '../model/orgScoreStore'

    export default {
        data() {
            return {
                scores: orgScoreStore.state.scores,
                max: 50
            }
        },
        methods: {
        },
        computed: {
            orgScore: function () {
                if (!this.scores.READ_ONLY_API_TOKEN) { return undefined }
                var score = 0
                if (this.scores.READ_ONLY_API_TOKEN) score += this.scores.READ_ONLY_API_TOKEN
                if (this.scores.MANAGE_USERS) score += this.scores.MANAGE_USERS
                if (this.scores.MANAGE_GROUPS) score += this.scores.MANAGE_GROUPS
                if (this.scores.MANAGE_APPS) score += this.scores.MANAGE_APPS
                if (this.scores.ASSIGN_GROUP_TO_APP) score += this.scores.ASSIGN_GROUP_TO_APP
                return score
            },
            stepProgress: function () {
                var progress = []
                if (this.scores.READ_ONLY_API_TOKEN) progress.push(1)
                if (this.scores.MANAGE_USERS) progress.push(2)
                if (this.scores.MANAGE_GROUPS) progress.push(3)
                if (this.scores.MANAGE_APPS) progress.push(4)
                if (this.scores.ASSIGN_GROUP_TO_APP) progress.push(5)

                return 'Steps Completed: ' + (progress.join(', ')) + ' of 5'
            },
            progressVariant: function () {
                if (this.orgScore < 50) return 'pink'
                return 'success'
            }
        }
    }
</script>